
import XHeader from 'vux/src/components/x-header/index.vue'


	import DocOne from '../components/doc/DocOne'
	import DocTwo from '../components/doc/DocTwo'
	import DocThree from '../components/doc/DocThree'
	import DocFour from '../components/doc/DocFour'



export default {
components: {
XHeader

},

data: function() {
return {
active: 0,
currentView: DocOne,

tabs: [{
type: '设备资料',
view: DocOne
},
{
type: '终端资料',
view: DocTwo
},
{
type: '企业资料',
view: DocThree
},
{
type: '备案信息',
view: DocFour
}
],


}
},
methods: {
back: function() {
window.history.back()
}
,
toggle(i, v) {
this.active = i
this.currentView = v
},




},

}



