
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
	components: {

		XHeader
	},
	data() {
		return {

		}
	},
	methods: {
		exit: function() {
			$.post('http://crane.u-xuan.com/Api/Api/Login/logout', function(status, msg) {
				if(status == 1) {
					alert(msg)
					location.href = '/login'
				} else {
					alert(msg)

				}

			}, 'json')

		}

	}
}



