import Qs from 'qs'

export default {

	data: function() {
		return {

			data: {

				status: '',
				img: ''

			},
			src: '',
			username: null,
			password: null,
			code: null

		}

	},
	methods: {
		//获取验证码
		getvercode: function() {
			var that = this

			$.ajax({
				url: 'http://crane.u-xuan.com/Api/Login/verify_code',
				dataType: 'json',
				type: 'post',
				data: {

				},
				success: function(e) {

					if(e.status == 1) {
						that.src = e.img

					} else {

					}
					return false;
				}
			})

		},
		//获取微信
		slideup: function() {
			$('#person_user').slideUp(200);
			$('#weixin').slideDown(200);
		},
		slidedown: function() {
			$('#person_user').slideDown(200);
			$('#weixin').slideUp(200);
		},
		//登陆
		login(ev) {
			ev.preventDefault()
			var that = this
			var data = { 'username': that.username, 'password': that.password, 'code': that.code };
			//验证输入手机号
			if(this.username == '') {
				//					Toast('请输用户名');
				$('#login_username').focus()
				return false;
			}

			if(this.password == '') {
				$('#login_password').focus()

				return false;
			}

			$.ajax({
				url: 'http://crane.u-xuan.com/Api/Login/login',
				dataType: 'json',
				type: 'post',
				data: {
					'username': this.username,
					'password': this.password
					//							'code': this.code,
				},
				success: function(e) {

					if(e.status == 1) {
						that.$router.push('/main');

					} else {
						alert(e.msg)
					}
					return false;
				}
			})

		}
	},
	mounted: function() {
		//			this.getvercode()

	}

}



