
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
	data: function() {
		return {
			code: '',
			name: '',
			address: ''
		}
	},
	components: {
		XHeader

	},
	methods: {
		subss: function(ev) {
			var that = this
			// 调取地图的经纬度值并传输此数值，判断的时候加上address
							if(this.code && this.name) {
			$.ajax({
				url: 'http://crane.u-xuan.com/Api/Engineering/engin_add',
				dataType: 'json',
				type: 'post',
				data: {
					code: that.code,
					name: that.name,
					address: that.address
				},
				success: function(e) {
					if(e.status == 1) {
						that.$router.push('/project');
					} else {

					}
					return false;
				}
			})

							}

		}

	}
}



