
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
	components: {
		XHeader

	},

	data: function() {
		return {
			data: [],
			time: '2017-06-27 00:00:00'
		}

	},

	methods: {
		getddata: function() {
			var that = this
			$.ajax({
				url: 'http://crane.u-xuan.com/Api/Mission/my_mission',
				dataType: 'json',
				type: 'post',
				data: {
					date: that.time
				},
				success: function(e) {

					if(e.status == 1) {

						that.data = e.data
						for(var i = 0; i < that.data.length; i++) {
							that.data[i].mission_type = '../../static/flyda_img/mywork/' + that.data[i].mission_type + '.png'

						}

					} else {

					}
					return false;
				}
			})

		},
		gettime: function() {
			var date = new Date()
			var month = (date.getMonth() + 1) < 10 ? ('0' + (date.getMonth() + 1)) : (date.getMonth() + 1)
			var dates = (date.getDate()) < 10 ? ('0' + (date.getDate())) : (date.getDate())
			this.time = date.getFullYear() + '-' + month + '-' + dates

		}

	},
	mounted() {
		this.gettime()
		$('.caldate').datepicker({})

		this.getddata()

	}
}



