
import XHeader from 'vux/src/components/x-header/index.vue'


//******************
export default {
	components: {

		XHeader

	},
	data: function() {
		return {

		}

	},

	methods: {
		exit: function() {
			var that = this
			$.ajax({
				url: 'http://crane.u-xuan.com/Api/Login/logout',
				dataType: 'json',
				type: 'post',
				data: {},
				success: function(e) {
					if(e.status == 1) {
						that.$router.push('/login');
					} else {
						alert(e.msg)
					}
					return false;
				}
			})

		}

	}

};



