
import MessonePage from '../components/mess/MessonePage'
import MessotwoPage from '../components/mess/MessotwoPage'
import MessthreePage from '../components/mess/MessthreePage'
import MessfourPage from '../components/mess/MessfourPage'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
	components: {
		XHeader

	},
	data: function() {
		return {
			active: 0,
			currentView: MessonePage,
			tabs: [{
					type: '任务提醒',
					view: MessonePage
				},
				{
					type: '通知',
					view: MessotwoPage
				},
				{
					type: '新闻资讯',
					view: MessthreePage
				},
				{
					type: '供求信息',
					view: MessfourPage
				}
			],

			isread: false,
			isStatus: true,
			src: '/static/flyda_img/alertlist/1.1.png',

		}
	},

	methods: {

		back: function() {
			window.history.back()
		},
		toggle(i, v) {
			this.active = i
			this.currentView = v
		}
	},
	created: function() {
		//页面加载完成发送请求获取数据

	}
}



