export default {
	data: function() {
		return {
			data: [],
			time: '2017-06-27 00:00:00'
		}

	},
	methods: {

		back: function() {
			window.history.back()
		},
		gettime: function() {

			var date = new Date()
			var month = (date.getMonth() + 1) < 10 ? ('0' + (date.getMonth() + 1)) : (date.getMonth() + 1)
			var dates = (date.getDate()) < 10 ? ('0' + (date.getDate())) : (date.getDate())
			this.time = date.getFullYear() + '/' + month + '/' + dates

		},
		minutime: function() {
			//得到当天的时间毫秒数
		var	newtime = (new Date(this.time)).getTime() - (24 * 60 * 60 * 60)
			//得到减去一天的新时间
			this.time = new Date(newtime)

		},
		addtime: function() {
			//得到当天的时间毫秒数
		var	newtime = (new Date(this.time)).getTime() + (24 * 60 * 60 * 60)
			//得到加上一天的新时间
			this.time = new Date(newtime)
		}

	},
	mounted() {
		this.gettime()

	}
}



