
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
	data: function() {
		return {
			code: '',
			type: '',
			xinghao: '',
			xuke: ''

		}
	},

	components: {
		XHeader

	},
	methods: {
		sub: function() {
			var that = this
			if(that.code && that.type && that.xinghao && that.xuke) {
				$.ajax({
					url: 'http://crane.u-xuan.com/Api/Facility/fac_add',
					dataType: 'json',
					type: 'post',
					data: {
						code: that.code,
						type: that.type,
						xinghao: that.xinghao,
						xuke: that.xuke
					},
					success: function(e) {

						if(e.status == 1) {
							that.$router.push('/machine');
						} else {

						}
						return false;
					}
				})

			}

		}

	}
}



