export default{
	data:function(){
		return{
				//发送请求获取的是所有的信息列表，详情页根据工程order动态绑定数据获取order重新发送请求
			datas: []
		}
	},
		methods: {
	
			getdatas: function() {

			var that = this
			$.ajax({
				url: 'urls/File/fac_file',
				dataType: 'json',
				type: 'post',
				data: {

				},
				success: function(e) {

					if(e.status == 1) {

						that.datas = e.data

					}
					else{
						$('.item').css('display','block')
					
						
					}
					return false;
				}
			})

		}
		
		
		
	},
	mounted(){
	 
		this.getdatas()
	}
	
	
}



