
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
		components: {
		XHeader

	},
	data: function() {
		return {
			info: {},

		}

	},
	methods: {
		getddata: function() {

			
			var that = this
			$.ajax({
				url: 'http://crane.u-xuan.com/Api/Mission/mission_detail',
				dataType: 'json',
				type: 'post',
				data: {
					id:this.$route.params.id
				},
				success: function(e) {

					if(e.status == 1) {

						that.info = e.data
						

					} else {

					}
					return false;
				}
			})

		}

	},
	mounted() {


		this.getddata()

	}
}



