
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
	components: {
		XHeader

	},

	data: function() {
		return {
			name: '',
			type: '',
			level: '',
			wenti: '',
			start: '',
			end: ''

		}

	},
	methods: {
		sub: function() {
			var that = this
			if(this.name && this.type && this.level && this.wenti && this.start && this.end) {
				$.ajax({
					url: 'http://crane.u-xuan.com/Api/Mission/mission_add',
					dataType: 'json',
					type: 'post',
					data: {
						name: this.name,
						type: this.type,
						level: this.level,
						wenti: this.wenti,
						start: this.start,
						end: this.end
					},
					success: function(e) {

						if(e.status == 1) {
							that.$router.push('/workpage');

						} else {

						}
						return false;
					}
				})

			}

		}

	},
	mounted() {

	}

}



