export default {
	data: function() {
		return {
			mobile: '',
			msgcode: '',
			password1: '',
			password2: ''

		}
	},

	methods: {
		//获取短信验证码
		getVerifyCode: function() {
			$.ajax({
				url: 'http://crane.u-xuan.com/Api/Login/sendMsgCode',
				dataType: 'json',
				type: 'post',
				data: {
					mobile:  this.mobile
				},
				success: function(e) {

					if(e.status == 1) {


						

					} else {

					}
					return false;
				}
			})

		},
		submit: function() {
			var that = this
			if(that.mobile == '') {

				return false;
			}

			var pattern = /^1[34578]\d{9}$/;

			if(pattern.test(that.mobile) == false) {

				return false;
			}

			if(that.msgcode == '') {

				return false;
			}

			layer.load(1, {
				shade: [0.1, '#000'] //0.1透明度的白色背景
			});

			
		
		
		
		
		
			return false;

		}
	}
}



