

import XHeader from 'vux/src/components/x-header/index.vue'


export default {
		components: {
		XHeader

	},
	
	
	
	data: function() {
		return {
			//发送请求获取的是所有的信息列表，详情页根据工程order动态绑定数据获取order重新发送请求
			datas: []

		}
	},
	methods: {
		back: function() {
			window.history.back()
		},
		getdatas: function() {

			var that = this
			$.ajax({
				url: 'http://crane.u-xuan.com/Api/Facility/fac_list',
				dataType: 'json',
				type: 'post',
				data: {

				},
				success: function(e) {

					if(e.status == 1) {

						that.datas = e.data

					}
					else{

						
					}
					return false;
				}
			})

		}
	},
		mounted(){
	 
		this.getdatas()
	}

}



