
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
	components: {
		XHeader

	},
	data: function() {
		return {
			datas: {},

		}

	},
	methods: {
		getddata: function() {

			var that = this
			$.ajax({
				url: 'http://crane.u-xuan.com/Api/Facility/fac_detail',
				dataType: 'json',
				type: 'post',
				data: {
					id: this.$route.params.id
				},
				success: function(e) {

					if(e.status == 1) {

						that.datas = e.data

					} else {

					}
					return false;
				}
			})

		}

	},
	mounted() {

		this.getddata()

	}
}



